<template>
  <div
    @mouseover="changestyle"
    @mouseleave="defaultstyle"
    @click="sendrecentact"
    ref="outlinebtn"
    class="outlinebtn"
    :style="{width:width+'px',height:height+'px',color:maincolor,borderColor:maincolor}"
  >{{btnname}}</div>
</template>

<script>
export default {
  props: ["width", "height", "btnname"],
  data() {
    return {
      testmaincolor: ""
      // maincolor
    };
  },
  created() {
    this.testmaincolor = this.maincolor;
  },
  computed: {
    hostinfo() {
      return this.$store.state.hostinfo[0];
    },
    maincolor() {
      return this.$store.state.maincolor;
    }
  },
  methods: {
    sendrecentact() {
      if (this.btnname == "") {
        var day2 = new Date();
        var day = day2.getDay();
        var minusday = 14 - day;
        var weekend = new Date();
        weekend.setTime(weekend.getTime() + 24 * 60 * 60 * 1000 * minusday);
        var sweekend =
          weekend.getFullYear() +
          "/" +
          (weekend.getMonth() + 1) +
          "/" +
          weekend.getDate();
        this.$store.commit("seteventtype", new Date(sweekend));
        this.$router.push({
          name: "Eventlistpage"
        });
      }
      if(this.btnname == ''){
        this.$emit('clearallhandler')
      }
    },
    changestyle() {
      let outlinebtn = this.$refs.outlinebtn;
      outlinebtn.style.backgroundColor = this.maincolor;
      outlinebtn.style.color = "white";
    },
    defaultstyle() {
      let outlinebtn = this.$refs.outlinebtn;
      outlinebtn.style.backgroundColor = "white";
      outlinebtn.style.color = this.maincolor;
    }
  },
  mounted() {
    // let maincolor = this.testmaincolor;
    // console.log("", maincolor);
