<template>
  <div class="signupeventlist">
    <div class="title" v-show="usereventinfo.length==0">
      <div class="loader">
        <span></span>
      </div>
    </div>

    <div class="eventblockwrap">
      <div v-if="usereventinfo">
        <eventblock
        
          :eventname="item[0]"
          :date="convertdate(item[2])"
          :id="item[18]"
          :time="item[3]"
          v-for="item of usereventinfo"
          :key="item[18]"
        ></eventblock>
      </div>
    </div>
   
  </div>
</template>

<script>
import eventblock from "../components/Profilepage-eventblock";


import anime from "animejs/lib/anime.es.js";
export default {
  data() {
    return {
      menuopen: false,
      currenteventstatus: "signup",
      alleventlist: [],
      filtersignuplist: [],
      dataloaded: false,
      eventtitle:''
    };
  },
  created() {
    this.$store.dispatch("gethostinfo", this.$route.params.hostid);
    this.$store.dispatch("getUserEventinfo");
  },
  mounted() {
    let circle = this.$refs.circle;
    anime({
      targets: circle,
      opcity: [1, 0],
      easing: "easeInOutQuad",
      loop: true,
      duration: 450
    });
  },
  methods: {
    opendropdown() {
      if (this.menuopen == false) {
        this.menuopen = true;
      } else {
        this.menuopen = false;
      }
    },
    clicked(val) {
      if (val == "history") {
        this.currenteventstatus = "history";
      } else {
        this.currenteventstatus = "signup";
      }
      this.menuopen = false;
    },
    convertdate(date) {
      let str = date;
      // console.log(str)
      let strlist = str.split("-");
      let result = (strlist[0] + "/" + strlist[1] + "/" + strlist[2]).substring(
        0,
        10
      );

      return result;
    },
    multiFilter(array, filters) {
      const filterKeys = Object.keys(filters);
      // filters all elements passing the criteria
      return array.filter(item => {
        // dynamically validate all filter criteria
        // console.log(item)
        return filterKeys.every(key => {
          //ignore when the filter is empty Anne
          if (!filters[key].length) return true;
          return !!~filters[key].indexOf(item[key]);
        });
      });
    }
  },
  computed: {
    hostid() {
      return this.$store.state.hostid;
    },
    usereventinfo() {
      return this.$store.state.usereventinfo;
    },
    userdata() {
      return this.$store.state.userprofiledata;
    }
  },
  components: {
    eventblock
  }
};
</script>

