<template>
  <div class="signupinfo">
    <div class="process">
      <div class="process-signup">
        <div class="number" :style="{backgroundColor:maincolor,borderColor:maincolor}">1</div>
        <span :style="{color:maincolor}"></span>
      </div>
      <i :style="{color:maincolor}" class="ri-arrow-right-s-line"></i>
      <div class="process-signup">
        <div class="number" :style="{borderColor:maincolor,color:maincolor}">2</div>
        <span class="submit" :style="{color:maincolor}"></span>
      </div>
    </div>
    <div class="singupblock">
      <signupinfoblock
        :parentsclearall="clearall"
        @test="catchval"
        style="marginTop:26px"
        v-for="(item,index) of ticketnumber"
        :key="index"
      ></signupinfoblock>
    </div>
    <outlinebtn @clearallhandler="emitclearall" btnname="" width="150" height="45"></outlinebtn>

    <div class="btnwrap">
      <fillbtn
        btnname=""
        width="147"
        height="43"
        class="firstbtn"
        @clickhandler="toeventpage"
      ></fillbtn>
      <fillbtn btnname="" width="147" height="43" @clickhandler="topaymentinfo"></fillbtn>
    </div>
  </div>
</template>

<script>
import signupinfoblock from "../components/signupinfo-block";
import fillbtn from "../components/fillbtn";
import outlinebtn from "../components/outlinebtn";
export default {
  data() {
    return {
      emitarr: [],
      clearall: false
    };
  },
  components: {
    signupinfoblock,
    fillbtn,
    outlinebtn
  },
  methods: {
    emitclearall() {
      this.clearall = true;
      setTimeout(() => {
        this.clearall = false;
      }, 500);
    },
    topaymentinfo() {
      console.log("");
      let ischeck = this.checkisfullfill();
      if (ischeck == true) {
        this.$store.commit("addsignupnumber");
        this.$store.commit("setpostsignupinfo", this.emitarr);
      } else {
        alert("!!");
      }
    },
    checkisfullfill() {
      let isfullfill;
      for (let i = 0; i < this.emitarr.length; i++) {
        if (this.emitarr[i] === "" || this.emitarr[i] === undefined) {
          isfullfill =  false;
          break;
         
        } else {
          isfullfill =  true;
          
        }
      }
      return isfullfill
    },

    toeventpage() {
      this.$router.push({
        name: "Eventpage",
        params: {
          id: this.$route.params.id
        }
      });

      // this.$store.commit('minussignupnumber');
    },
    catchval(val) {
      console.log("", val);
      this.emitarr = val;
      // this.$emit('test2',val)
    }
  },
  computed: {
    ticketnumber() {
      return this.$store.state.ticketnumber;
    },
    maincolor() {
      return this.$store.state.maincolor;
    }
  }
};
</script>

