<template>
  <div class="inputoutline">
    <div class="inputoutline-title">*{{title}}</div>
    <input ref="inputoutline" type="text" @input="changecontent" v-model="content" />
  </div>
</template>

<script>
export default {
  props: ["title", "currentindex", "alreadydata", "clearall"],
  data() {
    return {
      content: this.alreadydata,
      index: this.currentindex
    };
  },
  mounted() {
    let maincolor = this.maincolor;
    this.$refs.inputoutline.addEventListener("focus", function() {
      this.style.border = "solid 1px " + maincolor;
    });
    this.$refs.inputoutline.addEventListener("blur", function() {
      this.style.border = "solid 1px #818181";
    });
  },
  methods: {
    changecontent() {
      this.$emit("emitcontent", this.content, this.index);
    }
  },
  computed: {
    maincolor() {
      return this.$store.state.maincolor;
    }
  },
  watch: {
    clearall: {
      handler(newVal, oldVal) {
        if (newVal == true) {
          this.content = "";
        }
        if (newVal == false) {
          console.log(newVal, oldVal);
        }
      }
    }
  }
};
</script>

