import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Signup from "../views/Signup.vue";
import Profilepage from "../views/Profilepage.vue";
import Eventlistpage from "../views/EventListpage.vue";
// import HistoryPage from "../components/Historypage.vue";
// import SignupEventpage from "../components/Signupeventpage.vue";
// import Userinfo from '../components/Profilecontent.vue'
import MyactPage from '../views/MyactPage.vue'
import RecentEventlist from '../views/SendRecentEventinfo.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: "/:hostid",
    name: "Home",
    component: Home
  },{
    path:"/privateevent/:hostid/:eventid",
    name: "privateevent",
    component:Home
  },
  {
    path: "/signup/:id",
    name: "Signup",
    component: Signup,
  },
  {
    path:"/recenteventlist/:hostid",
    name:"RecentEventlist",
    component:RecentEventlist
  },
  {
    path: "/eventlistpage/:hostid",
    name: "Eventlistpage",
    component: Eventlistpage,
  },
  {
    path: "/profilepage/:hostid",
    name: "Profilepage",
    component: Profilepage
  },
  {
    path:"/myact/:hostid",
    name:"Myact",
    component:MyactPage
  },
  {
    path: "/eventpage/:id",
    name: "Eventpage",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Eventpage.vue"),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
