<template>
  <div class="profilepage">
    <Headerbar></Headerbar>
    <!-- <div class="bgp"></div> -->
    <div class="userpic">
      <div class="pic" :style="{backgroundImage:'url('+userdata.pictureUrl+')'}"></div>
    </div>
    <div class="switchtab">
      <div class="myact" :style="{color:maincolor}">
        <i :style="{color:maincolor}" class="ri-coupon-2-fill"></i>
        <span :style="{color:maincolor}"></span>
      </div>

      <router-link
        :to="{
                name:'Profilepage',
                params:{hostid:this.hostid}
            }"
      >
        <div class="profile">
          <i class="ri-user-fill"></i>
          <span></span>
        </div>
      </router-link>
    </div>
    <Signupeventpage></Signupeventpage>

    <Sidemenu v-show="showsidemenu"></Sidemenu>
    <cancelpage v-show="iscancelpopup" :title="canceleventtitle"></cancelpage>
  </div>
</template>

<script>
import Headerbar from "../components/Header";
import Signupeventpage from "../components/Signupeventpage";
import cancelpage from "../components/Cancelevent";
// import Profilecontent from '../components/Profilecontent'
import Sidemenu from "../components/Sidemenu";
export default {
  data() {
    return {
      currentstatus: "profile",
      showcancelpage: false
    };
  },
  components: {
    Headerbar,
    Sidemenu,

    Signupeventpage,
    cancelpage
  },
  created() {
    if (window.sessionStorage.getItem("profilecurrentpage")) {
      var profilecurrentpage = window.sessionStorage.getItem(
        "profilecurrentpage"
      );
      // console.log("", profilecurrentpage);
      this.currentstatus = profilecurrentpage;
    }
    this.$store.dispatch("gethostinfo", this.$route.params.hostid);
  },
  mounted() {
    this.$store.commit("sethostid", this.$route.params.hostid);
  },
  methods: {
    switchtab(val) {
      this.currentstatus = val;
      window.sessionStorage.setItem("profilecurrentpage", val);
    }
  },
  computed: {
    iscancelpopup() {
      return this.$store.state.iscancelpopup;
    },
    maincolor() {
      return this.$store.state.maincolor;
    },
    profilecurrentpage() {
      return this.$store.state.profilecurrentpage;
    },
    userdata() {
      return this.$store.state.userprofiledata;
    },
    showsidemenu() {
      return this.$store.state.showsidemenu;
    },
    hostid() {
      return this.$store.state.hostid;
    },
    canceleventtitle() {
      return this.$store.state.canceltitle;
    }
  }
};
</script>

