<template>
  <div class="eventlistpage" ref="eventlistpage">
    <Headerbar></Headerbar>
    <br />
    <div class="searchbarwrap">
      <SearchbarRound :alreadydata="this.keyword" @roundemitkeyword="catchkeyword"></SearchbarRound>
    </div>
    <div class="searchfilterwrap">
      <Searchfilter
        @emittypefilter="typefilter"
        @emittimefilter="timefilter"
        @emitpricefilter="pricefilter"
        @sendolderangeltype="olderangelfilter"
      ></Searchfilter>
    </div>

    <div class="result">
      <div v-if="keyword==''"></div>
      <div v-else>{{keyword}}</div>
      <div v-if="currenttype==''"></div>
      <div v-else>
        
        <span class="currenttype" :style="{backgroundColor:maincolor}">{{currenttype}}</span>
      </div>
      <div v-if="eventtime==''"></div>
      <div v-else>
        
        <span>{{showeventtime}}</span>
      </div>
      <div v-show="price===''"></div>
      <div v-show="price===0"></div>
      <div v-show="price!=0&&price!=''">
        
        <span>{{price}}</span>
      </div>
    </div>
    <!-- <div class="catawrap">
        <catatag :class="{tagclick:currenttagindex==index}" @typehandler="selecttype" v-for="(item,index) of taglist" :key="index" :tagname="item" :tagindex=index></catatag>
    </div>-->

    <div class="eventlist">
      <!-- <h4 v-show="filtereventlist.length==0&&keyword!=''"></h4> -->
      <h4 v-show="filtereventlist.length==0"></h4>
      <Actblock
        v-show="item[5]==''||item[5]==''"
        v-for="item in filtereventlist"
        :key="item[18]"
        :name="item[0]"
        :fee="item[13]"
        :loc="item[4]"
        :date="convertdate(item[2])"
        :time="item[3]"
        :number="item[7]"
        :deadlinedate="convertdate(item[8])"
        :type="item[6]"
        :eventid="item[18]"
        :pic="item[1]"
        :isolderangelevent="item[16]"
      ></Actblock>

      <!-- <Actblockeventlist v-for="item of filtereventlist" :key="item[17]" :name="item[0]" :loc="item[4]" :date="convertdate(item[2])" :time="item[3]" :actin="item[7]" :waitingnum="item[8]" :type="item[6]" :id="item[17]" :pic="item[1]"></Actblockeventlist> -->
    </div>
    <h4 v-show="dataloaded==true">...</h4>
    <h4 v-show="intheend==true"></h4>

    <Footer></Footer>

    <Sidemenu v-show="showsidemenu"></Sidemenu>
    <div
      v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 'onVisible')"
    ></div>
    <!-- <div class="loadingmask" v-if="!hostinfo||filtereventlist.length==0">
      <div class="loader" :style="{borderTop:'8px solid'+maincolor}"></div>
    </div>-->

    <!-- <div v-observe-visibility="visibilityChanged" ></div> -->
  </div>
</template>

<script>
import Headerbar from "../components/Header";
import Actblock from "../components/Actblock";
import Searchfilter from "../components/Searchfilter";
// import Searchbar from '../components/Searchbar'
import Footer from "../components/footer";
import SearchbarRound from "../components/searchbar-round";
// import catatag from '../components/catatag'
// import Actblockeventlist from '../components/Actblock-eventlist'
import axios from "axios";
import Sidemenu from "../components/Sidemenu";
import { ObserveVisibility } from "vue-observe-visibility";
// import { filter } from "vue/types/umd";

export default {
  directives: {
    ObserveVisibility
  },
  data() {
    return {
      taglist: ["", "", "", "", "3C"],
      eventlist: [],
      keyword: this.$store.state.searchkeyword,
      currenttype: "",
      eventtime: this.$store.state.eventtype,
      showeventtime: "",
      currenttagindex: "-1",
      loadingcircle: true,
      intheend: false,
      watchlist: [],
      price: "",
      page: 1,
      isolderangelevent: "",
      dataloaded: false
    };
  },
  components: {
    Headerbar,
    // Searchbar,
    // catatag,
    // Actblockeventlist,
    Sidemenu,
    SearchbarRound,
    Actblock,
    Searchfilter,
    Footer
    // ObserveVisibility
  },
  created() {
    axios({
      method: "get",
      url:
        "https://script.google.com/macros/s/AKfycbzCPwCEMst1ZH_3o6rvN_SFcu-e2VrUUtifrIyXbebjT3XhIkpW2Seom10jckfmOZE6pw/exec",
      params: {
        page: this.page,
        hostid: this.$route.params.hostid
      }
    }).then(response => {
      this.loadingcircle = false;
      this.eventlist = response.data;
      this.$store.dispatch("gethostinfo", this.$route.params.hostid);
      this.$store.commit("sethostid", this.$route.params.hostid);
    });
    var day2 = new Date();
    day2.setTime(day2.getTime());
    var day = day2.getDay();
    var minusday = 7 - day;
    var nexweek = new Date();
    nexweek.setTime(nexweek.getTime() + 24 * 60 * 60 * 1000 * (minusday + 7));
    var snexweek =
      nexweek.getFullYear() +
      "/" +
      (nexweek.getMonth() + 1) +
      "/" +
      nexweek.getDate();
    console.log(this.eventtime);
    console.log(new Date(snexweek));
    if(this.eventtime!=''){
      if (this.eventtime.getTime() == new Date(snexweek).getTime()) {
      this.showeventtime=''
    }
    }
    
  },

  mounted() {
    
    // window.sessionStorage.setItem("hostinfo", JSON.stringify(this.hostinfo));
    // if (window.sessionStorage.getItem("hostinfo")) {
    //   var hostinfo = window.sessionStorage.getItem("hostinfo");
    //   this.$store.commit("sethostinfo", JSON.parse(hostinfo));
    // }
  },
  methods: {
    async getEventinfo() {
      let eventinfo = await axios({
        method: "get",
        url:
          "https://script.google.com/macros/s/AKfycbzCPwCEMst1ZH_3o6rvN_SFcu-e2VrUUtifrIyXbebjT3XhIkpW2Seom10jckfmOZE6pw/exec",
        params: {
          page: this.page,
          hostid: this.$route.params.hostid
        }
      }).then(response => {
        // console.log("", response.data);
        this.intheend = this.eventlist.length == response.data.length;
        this.eventlist = response.data;

        this.dataloaded = false;
        // this.loadingcircle = false;
        // this.eventlist = response.data;
        // this.$store.dispatch("gethostinfo",this.$route.params.hostid);
        // this.$store.commit("sethostid", this.$route.params.hostid);
      });
      console.log(eventinfo);
    },
    olderangelfilter() {
      this.currenttype = "";
      this.isolderangelevent = "";
    },
    visibilityChanged(isVisible) {
      // this.dataloaded = true
      if (!isVisible) {
        return;
      } else if (this.page >= 10) {
        return;
      } else if (this.intheend == true) {
        return;
      } else {
        this.dataloaded = true;
        this.page++;
        this.getEventinfo();
      }

      // console.log("");
    },
    typefilter(val) {
      if (val == "") {
        this.currenttype = "";
      } else {
        this.currenttype = val;
      }
    },
    timefilter(val) {
      this.showeventtime = val;
      if (val == "") {
        this.eventtime = "";
      } else {
        // this.eventtime = val;
        //
        var day2 = new Date();
        day2.setTime(day2.getTime());
        var s2 =
          day2.getFullYear() +
          "/" +
          (day2.getMonth() + 1) +
          "/" +
          day2.getDate();
        //
        var day3 = new Date();
        day3.setTime(day3.getTime() + 24 * 60 * 60 * 1000);
        var s3 =
          day3.getFullYear() +
          "/" +
          (day3.getMonth() + 1) +
          "/" +
          day3.getDate();

        var day4 = new Date();
        day4.setTime(day4.getTime() + 24 * 60 * 60 * 1000 * 2);
        var s4 =
          day4.getFullYear() +
          "/" +
          (day4.getMonth() + 1) +
          "/" +
          day4.getDate();
        var day = day2.getDay();
        var minusday = 7 - day;

        var weekend = new Date();
        weekend.setTime(weekend.getTime() + 24 * 60 * 60 * 1000 * minusday);
        var sweekend =
          weekend.getFullYear() +
          "/" +
          (weekend.getMonth() + 1) +
          "/" +
          weekend.getDate();

        var nexweek = new Date();
        nexweek.setTime(
          nexweek.getTime() + 24 * 60 * 60 * 1000 * (minusday + 7)
        );
        var snexweek =
          nexweek.getFullYear() +
          "/" +
          (nexweek.getMonth() + 1) +
          "/" +
          nexweek.getDate();

        switch (val) {
          case "":
            this.eventtime = new Date(s2);

            break;
          case "":
            this.eventtime = new Date(s3);

            break;
          case "":
            this.eventtime = new Date(s4);

            break;
          case "":
            this.eventtime = new Date(sweekend);

            break;
          case "":
            this.eventtime = new Date(snexweek);

            break;
        }
      }

      // return this.filtereventlist
    },
    pricefilter(val) {
      console.log(val);
      switch (val) {
        case "":
          this.price = "";
          break;
        case "":
          this.price = parseInt(0, 10);
          // this.price = 0;
          break;
        case "500":
          this.price = 500;

          break;
        case "1000":
          this.price = 1000;

          break;
        case "1500":
          this.price = 1500;

          break;
      }
    },
    searchkeyword(val) {
      this.keyword = val;
    },
    catchkeyword(val) {
      this.keyword = val;
    },
    selecttype(val, index) {
      if (this.currenttagindex == index) {
        this.currenttagindex = -1;
        this.currenttype = "";
      } else {
        this.currenttagindex = index;
        this.currenttype = val;
      }
    },
    convertdate(date) {
      let str = date;

      let strlist = str.split("-");
      let day = parseInt(strlist[2], 10) + 1 + "";

      let result = (strlist[0] + "/" + strlist[1] + "/" + day).substring(0, 10);

      return result;
    },
    filterkeyword(alleventlist) {
      return alleventlist.filter(item => {
        return item[0].search(this.keyword) != -1;
      });
    },
    filtertime(alleventlist) {
      var day2 = new Date();
      day2.setTime(day2.getTime());
      var s2 =
        day2.getFullYear() + "/" + (day2.getMonth() + 1) + "/" + day2.getDate();
      return alleventlist.filter(item => {
        return (
          new Date(this.convertdate(item[2])) <= this.eventtime &&
          new Date(this.convertdate(item[2])) >= new Date(s2)
        );
      });
    },
    filtertype(alleventlist) {
      return alleventlist.filter(item => {
        if (this.currenttype == "") {
          return item[16] == "";
        } else {
          return item[6] == this.currenttype;
        }
      });
    },
    filterprice(alleventlist) {
      if (this.price == 0) {
        return alleventlist.filter(item => {
          return item[13] == 0;
        });
      } else {
        return alleventlist.filter(item => {
          return item[13] <= this.price;
        });
      }
    },
    filterolderangelevent(alleventlist) {
      return alleventlist.filter(item => {
        return item[16] === this.isolderangelevent;
      });
    }
  },
  computed: {
    selecteventtype() {
      return this.$store.state.eventtype;
    }, 
    hostinfo() {
      return this.$store.state.hostinfo[0];
    },
    maincolor() {
      return this.$store.state.maincolor;
    },
    filtereventlist() {
      const { keyword, currenttype, eventtime, price } = this;
      // const returnarr = [];
      if (
        (keyword != "" &&
          currenttype != "" &&
          eventtime != "" &&
          price != "") ||
        (keyword != "" && currenttype != "" && eventtime != "" && price === 0)
      ) {
        return this.eventlist
          .filter(item => item[0].includes(keyword) == true)
          .filter(item => item[6] == currenttype)
          .filter(item => new Date(this.convertdate(item[2])) <= eventtime)
          .filter(item => item[13] <= price);
      }
      if (
        (keyword != "" && currenttype != "" && price != "") ||
        (keyword != "" && currenttype != "" && price === 0)
      ) {
        console.log("4");
        return this.filterkeyword(
          this.filtertype(this.filterprice(this.eventlist))
        );
      }
      if (
        (keyword != "" && eventtime != "" && price != "") ||
        (keyword != "" && eventtime != "" && price === 0)
      ) {
        console.log("");
        return this.filterkeyword(
          this.filtertime(this.filterprice(this.eventlist))
        );
      }
      if (
        (currenttype != "" && eventtime != "" && price != "") ||
        (currenttype != "" && eventtime != "" && price === 0)
      ) {
        return this.filtertype(
          this.filtertime(this.filterprice(this.eventlist))
        );
      }
      if (keyword != "" && currenttype != "" && eventtime != "") {
        console.log("4");
        return this.filterkeyword(
          this.filtertype(this.filtertime(this.eventlist))
        );
      }
      if (keyword != "" && currenttype != "") {
        return this.filterkeyword(this.filtertype(this.eventlist));
      }
      if (keyword != "" && eventtime != "") {
        return this.filterkeyword(this.filtertime(this.eventlist));
      }
      if ((keyword != "" && price != "") || (keyword != "" && price === 0)) {
        return this.filterkeyword(this.filterprice(this.eventlist));
      }
      if (currenttype != "" && eventtime != "") {
        return this.filtertype(this.filtertime(this.eventlist));
      }
      if (
        (currenttype != "" && price != "") ||
        (currenttype != "" && price === 0)
      ) {
        console.log("10");
        return this.filtertype(this.filterprice(this.eventlist));
      }
      if (
        (eventtime != "" && price != "") ||
        (eventtime != "" && price === 0)
      ) {
        return this.filtertime(this.filterprice(this.eventlist));
      }
      if (keyword != "") {
        console.log("1");
        return this.filterkeyword(this.eventlist);
      }
      if (currenttype != "") {
        console.log("2");
        return this.filtertype(this.eventlist);
      }
      if (eventtime != "") {
        console.log("3");
        return this.filtertime(this.eventlist);
      }
      if (price != "" || price === 0) {
        console.log("6");
        return this.filterprice(this.eventlist);
      } else {
        return this.eventlist;
      }

      // console.log("5");
    },
    showsidemenu() {
      return this.$store.state.showsidemenu;
    }
  }
};
</script>

