<template>
  <div
    ref="profileeventpage"
    class="profileeventpage"
    @click="toeventpage"
    @mouseover="changestyle"
  >
    <div class="title">{{eventname}}</div>
    <div class="date">
      <div class="wrap">
        <div :style="{backgroundColor:maincolor}"></div>
        <span>{{date}} {{time}}</span>
      </div>

      <div
        class="cancelbtn"
        :style="{borderColor:maincolor,color:maincolor}"
        @click.stop="cancelregis"
      ></div>
    </div>
  </div>
</template>

<script>

export default {
  props: ["eventname", "date", "time", "id"],
  data() {
    return {};
  },
  computed: {
    maincolor() {
      return this.$store.state.maincolor;
    },
    userdata() {
      return this.$store.state.userprofiledata;
    },
  },
  methods: {
    toeventpage() {
      this.$router.push({
        name: "Eventpage",
        params: {
          id: this.id
        }
      });
    },
    cancelregis() {
      console.log('')
      let data = {
        status:true,
        title:this.eventname
      }
      this.$store.commit('setcancelpopup',data)
      this.$store.commit('setcanceleventid',this.id)
      
    },
    changestyle() {
      let profileeventpage = this.$refs.profileeventpage;
      profileeventpage.style.borderColor = this.maincolor;
    }
  }
};
</script>

